import "primeicons/primeicons.css";
import "primereact/resources/themes/saga-blue/theme.css";
import "primereact/resources/primereact.css";
import "primeflex/primeflex.css";
import "../index.css";
import ReactDOM from "react-dom";

import React, { Component } from "react";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Toast } from "primereact/toast";
import { Button } from "primereact/button";
import { Toolbar } from "primereact/toolbar";
import { InputText } from "primereact/inputtext";

class SearchAnalyticsTable extends Component {
	emptyProduct = {
		billno: null,
		category: null,
		weight: 0,
		location: "",
		price: 0,
		tdate: "",
	};

	constructor(props) {
		super(props);
		this.state = {
			products: this.props.dataFromParent,
			productDialog: false,
			deleteProductDialog: false,
			deleteProductsDialog: false,
			product: this.emptyProduct,
			submitted: false,
			globalFilter: null,
		};

		this.cols = [
			{ field: "CONSIGNMENT_NUMBER", header: "Bill Number" },
			{ field: "CATEGORY", header: "Category" },
			{ field: "PARCEL_WEIGHT", header: "Weight" },
			{ field: "PLACE", header: "Location" },
			{ field: "PARCEL_RATE", header: "Price" },
			{ field: "BOOKING_DATE", header: "Date" },
		];

		this.exportColumns = this.cols.map((col) => ({
			title: col.header,
			dataKey: col.field,
		}));

		this.rightToolbarTemplate = this.rightToolbarTemplate.bind(this);
		this.priceBodyTemplate = this.priceBodyTemplate.bind(this);

		this.exportCSV = this.exportCSV.bind(this);
		this.exportPdf = this.exportPdf.bind(this);
	}

	findIndexById(billno) {
		let index = -1;
		for (let i = 0; i < this.state.products.length; i++) {
			if (this.state.products[i].billno === billno) {
				index = i;
				break;
			}
		}

		return index;
	}
	exportPdf() {
		import("jspdf").then((jsPDF) => {
			import("jspdf-autotable").then(() => {
				const doc = new jsPDF.default(0, 0);
				doc.autoTable(
					this.exportColumns,
					JSON.parse(this.props.dataFromParent)
				);
				doc.save("products.pdf");
			});
		});
	}
	exportCSV() {
		this.dt.exportCSV();
	}

	rightToolbarTemplate() {
		return (
			<React.Fragment>
				<Button
					label="Export PDF"
					type="button"
					icon="pi pi-file-pdf"
					onClick={this.exportPdf}
					className="p-button-help p-mr-2"
					data-pr-tooltip="PDF"
				/>
				<Button
					label="Export Excel"
					icon="pi pi-upload"
					className="p-button-success p-mr-2"
					onClick={this.exportCSV}
					data-pr-tooltip="PDF"
				/>
			</React.Fragment>
		);
	}

	priceBodyTemplate(rowData) {
		return this.formatCurrency(rowData.price);
	}
	render() {
		const header = (
			<div className="table-header">
				<span className="p-input-icon-left">
					<i className="pi pi-search" />
					<InputText
						type="search"
						onInput={(e) => this.setState({ globalFilter: e.target.value })}
						placeholder="Search..."
					/>
				</span>
			</div>
		);

		return (
			<div className="datatable-crud-demo">
				<Toast ref={(el) => (this.toast = el)} />

				<div className="card">
					<DataTable
						ref={(el) => (this.dt = el)}
						value={JSON.parse(this.props.dataFromParent)}
						dataKey="id"
						paginator
						rows={10}
						rowsPerPageOptions={[5, 10, 25]}
						paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
						currentPageReportTemplate="Showing {first} to {last} of {totalRecords} products"
						globalFilter={this.state.globalFilter}
						header={header}
					>
						<Column field="CAWBNO" header="Bill Number" sortable></Column>
						<Column field="CPRODCODE" header="Category" sortable></Column>
						<Column field="NACTWGT" header="Actual Weight" sortable></Column>
						<Column field="NCHRGWT" header="Charge Weight" sortable></Column>
						<Column field="NTOTALAMT" header="Amount" sortable></Column>
						<Column field="BOOKING_DATE" header="MHS Date" sortable></Column>
						<Column field="PARCEL_RATE" header="MHS Amount" sortable></Column>
						<Column field="PLACE" header="MHS Place" sortable></Column>
						<Column field="PARCEL_WEIGHT" header="MHS Weight" sortable></Column>
						<Column field="Profit" header="MHS Profit" sortable></Column>
					</DataTable>
					<Toolbar
						className="p-mb-4"
						right={this.rightToolbarTemplate}
					></Toolbar>
				</div>
			</div>
		);
	}
}

export default SearchAnalyticsTable;
